// Print

@import '_fonts';
@import '_functions';
@import '_mixins';
@import '_colors';
@import '_general';

body {
    background: $white; }

.service-print {
    padding: 2rem;
    .header {
        @include clear;
        border-bottom: 1px solid $black;
        margin-bottom: 1rem;
        width: 100%;
        .logo {
            float: left;
            padding: 1rem;
            text-align: left;
            width: 60%;
            .img {
                width: rem(70px); }
            .title {
                @include font-size(20px);
                display: inline-block;
                margin-left: 1rem; } }
        .folio {
            float: left;
            padding: 1rem;
            text-align: right;
            width: 40%;
            .number {
                @include font-size(16px);
                border: 1px solid $black;
                border-radius: rem(5px);
                display: inline-block;
                padding: 1rem;
                text-align: center;
                span {
                    display: block;
                    &.title {
                        font-weight: 600; } } } }
        .info {
            border-top: 1px solid $black;
            float: left;
            padding: .5rem 0;
            width: 100%;
            .data {
                @include font-size(14px);
                display: inline-block;
                font-weight: 400;
                padding: .2rem;
                width: 49%; } } }
    .service-table {
        border-collapse: collapse;
        margin-bottom: .5rem;
        width: 100%;
        thead {
            tr {
                th {
                    background: $gray;
                    color: $black;
                    @include font-size(14px);
                    font-weight: 400;
                    padding: .5rem;
                    text-align: left;
                    text-transform: uppercase; } } }
        tbody,
        tfoot {
            tr {
                &:nth-child(odd) {
                    background: #f8f8f8; }
                td {
                    @include font-size(14px);
                    padding: .5rem;
                    .equipment-title {
                        @include font-size(14px);
                        font-weight: 600; }
                    .equipment-brand,
                    .equipment-group,
                    .equipment-warehouse, {
                        @include font-size(12px); }
                    .equipment-description {
                        @include font-size(13px); }
                    .price {
                        @include font-size(14px);
                        font-weight: 400;
                        &.with-sale {
                            text-decoration: line-through; } }
                    .qty {
                        @include font-size(16px); } } } } }
    .footer {
        @include font-size(12px);
        margin-top: 1rem;
        text-align: center;
        .address {
            width: 100%; }
        .contact {
            span {
                display: inline-block;
                padding: 0 2rem; } } } }
